import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import { getReservationHistory } from '../services/reservation';
import '../styles/global.css'; // Importando o CSS global
const Historico: React.FC = () => {
  const [history, setHistory] = useState([]);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchHistory = async () => {
      try {
        const data = await getReservationHistory(); // Chamada ao serviço mockado
        setHistory(data);
      } catch (err) {
        setError('Erro ao carregar o histórico de reservas.');
      }
    };

    fetchHistory();
  }, []);

  return (
    <div style={{ display: 'flex'}}>
      <Sidebar />
      <div style={{ marginLeft: '250px', padding: '20px', width: '100%' }}>
        <h1 style={{ color: '#333', borderBottom: '2px solid #ddd', paddingBottom: '10px' }}>Histórico de Reservas</h1>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <ul style={{ listStyle: 'none', padding: '0', margin: '20px 0' }}>
          {history.map((item: any) => (
            <li
              key={item.id}
              style={{
                padding: '10px 15px',
                margin: '10px 0',
                backgroundColor: '#f9f9f9',
                border: '1px solid #ddd',
                borderRadius: '5px',
              }}
            >
              <strong>{item.user}</strong> reservou o <em>{item.environment}</em> no dia{' '}
              {new Date(item.date).toLocaleDateString('pt-BR')} às{' '}
              {new Date(item.date).toLocaleTimeString('pt-BR', { hour: '2-digit', minute: '2-digit' })}.{' '}
              Status: <strong>{item.status}</strong>.
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Historico;
